import React, { useState } from "react";
import Popup from "../Popup";

const ChangeFieldPopup = ({
  title,
  label,
  isPassword = false,
  onSave,
  onClose
}) => {
  const [oldPassword, setOldPassword] = useState("");
  const [newValue, setNewValue] = useState("");
  const [confirmValue, setConfirmValue] = useState("");
  const [error, setError] = useState("");

  const isValidPassword = (password) => {
    const passwordRegex =
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/;
    return passwordRegex.test(password);
  };

  const handleSubmit = async () => {
    setError("");

    if (!newValue || !confirmValue || (isPassword && !oldPassword)) {
      setError("Wszystkie pola musz by wypenione.");
      return;
    }

    if (newValue !== confirmValue) {
      setError("Wprowadzone wartoci si rni.");
      return;
    }

    if (isPassword && !isValidPassword(newValue)) {
      setError("Haso musi mie min. 8 znakw, wielk i ma liter, cyfr i znak specjalny.");
      return;
    }

    try {
      if (isPassword) {
        onSave({ oldPassword, newPassword: newValue });
      } else {
        onSave(newValue);
      }
      onClose();
    } catch (err) {
      console.error("Bd zmiany pola:", err);
      setError("Wystpi bd podczas aktualizacji.");
    }
  };

  return (
    <Popup title={title} onClose={onClose}>
      <div className="change-field-popup">
        {isPassword && (
          <div className="change-field-group">
            <label className="change-field-label">Obecne haso</label>
            <input
              className="change-field-input"
              type="password"
              value={oldPassword}
              onChange={(e) => setOldPassword(e.target.value)}
            />
          </div>
        )}
        <div className="change-field-group">
          <label className="change-field-label">{label}</label>
          <input
            className="change-field-input"
            type={isPassword ? "password" : "text"}
            value={newValue}
            onChange={(e) => setNewValue(e.target.value)}
          />
        </div>
        <div className="change-field-group">
          <label className="change-field-label">Powtrz {label.toLowerCase()}</label>
          <input
            className="change-field-input"
            type={isPassword ? "password" : "text"}
            value={confirmValue}
            onChange={(e) => setConfirmValue(e.target.value)}
          />
        </div>
        {error && <div className="error-message">{error}</div>}
        <div className="save-buttons">
          <button className="profile-save-button" onClick={handleSubmit}>Zapisz</button>
          <button className="profile-save-button" onClick={onClose}>Anuluj</button>
        </div>
      </div>
    </Popup>
  );
};

export default ChangeFieldPopup; 